import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Input,
  NgZone,
  OnInit,
  Optional,
  Renderer2,
  Self,
  ViewChild
} from '@angular/core';
import {NgControl} from '@angular/forms';
import {DOWN_ARROW, UP_ARROW} from '@angular/cdk/keycodes';
import {animate, AnimationEvent, keyframes, style, transition, trigger} from '@angular/animations';
import {BehaviorSubject, EMPTY, fromEvent, merge} from 'rxjs';
import {debounceTime, distinctUntilChanged, filter, switchMap, takeUntil} from 'rxjs/operators';
import {BigNumber} from 'bignumber.js';
import {isNil} from 'lodash-es';

import {ANIMATION_DURATION, ANIMATION_TIMING_FUNCTION} from '../../common/animation';
import {
  AdaptRadarAngularComponentRxCounterData,
  AdaptRadarAngularCustomEventName,
  AdaptRadarAngularCustomEventType,
  AdaptRadarAngularService
} from '../../common/radar/adapt-radar';
import {isSafeNumber} from '../../common/utilities';
import {KeyValueObject} from '../../common/common.models';
import {getEventKeyCode} from '../../common/key-codes';
import {AdaptDeviceDetectionService} from '../../common/device-detection.service';
import {AdaptTranslateService} from '../../common/i18n/index';
import {CounterOperations} from '../../common/common.enums';

import {AdaptRxFeedbackComponent, AdaptRxFormControlDirective, FormControlSize} from '../form-control/index';
import {AdaptTooltipDirective} from '../../tooltip/index';

import {AdaptRxCounterConfigService} from './adapt-rx-counter-config.service';
import {CounterTextsModel} from './adapt-rx-counter.models';

@Component({
  selector: 'adapt-rx-counter',
  templateUrl: './adapt-rx-counter.component.html',
  animations: [
    trigger('highlightMobileIncrease', [
      transition(':enter', [
        style({bottom: 0, opacity: 0}),
        animate(ANIMATION_DURATION.fast + ' ' + ANIMATION_TIMING_FUNCTION.base.transitionInOut,
          style({bottom: 'calc(100% + 10px)', opacity: 1})
        )
      ]),
      transition(':leave', [
        style({bottom: 'calc(100% + 10px)', opacity: 1}),
        animate('.12s',
          style({bottom: 0, opacity: 0})
        )
      ])
    ]),
    trigger('highlightMobileDecrease', [
      transition(':enter', [
        style({top: 0, opacity: 0}),
        animate(ANIMATION_DURATION.fast + ' ' + ANIMATION_TIMING_FUNCTION.base.transitionInOut,
          style({top: 'calc(100% + 10px)', opacity: 1})
        )
      ]),
      transition(':leave', [
        style({top: 'calc(100% + 10px)', opacity: 1}),
        animate('.12s',
          style({top: 0, opacity: 0})
        )
      ])
    ]),
    trigger('errorToggleAnimation', [
      transition(':enter', [
        style({
          opacity: 0
        }),
        animate(250)
      ]),
      transition(':leave', [
        style({
          opacity: 1
        })
      ])
    ]),
    trigger('highlightArrow', [
      transition('0 => 1',
        animate(ANIMATION_DURATION.medium,
          keyframes([
            style({color: '*', offset: 0}),
            style({color: '#00867E', offset: 1})
          ]))
      )
    ])]
})
export class AdaptRxCounterComponent extends AdaptRxFormControlDirective implements OnInit, AfterViewInit {
  protected controlName: string = 'counter';

  analyticsComponentName: typeof AdaptRadarAngularCustomEventName = AdaptRadarAngularCustomEventName;

  counterTextsDefault: CounterTextsModel = {
    decrease: '',
    increase: ''
  };
  animateArrowUp: boolean = false;
  animateArrowDown: boolean = false;
  increaseAnimationDone: boolean = false;
  decreaseAnimationDone: boolean = false;
  focused: boolean;
  isMobile: boolean;
  isSpinnerMobile: boolean;

  /**
   * Sets the prefix label to the field.
   * @docs-default
   */
  @Input() prefix: string;

  /**
   * Sets the suffix label to the field.
   * @docs-default
   */
  @Input() suffix: string;

  /**
   * The max value to be set to the counter via spinners increase button.
   * @docs-default
   */
  @Input() max: number | BigNumber;

  /**
   * The min value to be set to the counter via spinners increase button.
   * @docs-default
   */
  @Input() min: number | BigNumber;

  @Input() step: number;

  /**
   * Control size. Available options are: 'default', 'sm', 'lg'.
   * @docs-default default
   */
  @Input() size: FormControlSize;

  /**
   * The placeholder string to be shown.
   * @docs-default
   */
  @Input() placeholder: string;

  /**
   * Use disable styles for readonly state.
   * @docs-default false
   */
  @Input() disabledStyleForReadonlyState: boolean = false;

  @ViewChild('input', {static: true}) inputEl: ElementRef<HTMLInputElement>;
  @ViewChild('mobileActionsContainer', {static: false}) mobileActionsContainer: ElementRef<HTMLInputElement>;

  /**
   * Reference on AdaptRxFeedbackComponent
   */
  @ViewChild('adaptRxFeedbackRef', {static: true}) adaptRxFeedbackRef: AdaptRxFeedbackComponent;

  /**
   * Check if the control has UI error state
   */
  get hasDangerState(): boolean {
    return this.adaptRxFeedbackRef.hasUIErrorState;
  }

  get value(): string {
    return this._value || this._value === 0 ? this._value.toString() : '';
  }

  get precision(): number {
    if (this.step % 1 === 0) {
      return null;
    }

    return this.step.toString().split(/[.]/)[1].length;
  }

  get inputSize(): number {
    return (this._value && this._value.toString().length) || 1;
  }

  private _value: number | BigNumber;
  private _removeDocumentClickListener: Function;

  private spinnerUp$: BehaviorSubject<ElementRef<HTMLButtonElement>> = new BehaviorSubject(null);
  private spinnerDown$: BehaviorSubject<ElementRef<HTMLButtonElement>> = new BehaviorSubject(null);
  private mobileSpinnerUp$: BehaviorSubject<ElementRef<HTMLButtonElement>> = new BehaviorSubject(null);
  private mobileSpinnerDown$: BehaviorSubject<ElementRef<HTMLButtonElement>> = new BehaviorSubject(null);

  @ViewChild('spinnerUp', {static: false})
  get spinnerUp(): ElementRef<HTMLButtonElement> {
    return this.spinnerUp$.value;
  }

  set spinnerUp(elementRef: ElementRef<HTMLButtonElement>) {
    this.spinnerUp$.next(elementRef);
  }

  @ViewChild('spinnerDown', {static: false})
  get spinnerDown(): ElementRef<HTMLButtonElement> {
    return this.spinnerDown$.value;
  }

  set spinnerDown(elementRef: ElementRef<HTMLButtonElement>) {
    this.spinnerDown$.next(elementRef);
  }

  @ViewChild('mobileSpinnerUp', {static: false})
  get mobileSpinnerUp(): ElementRef<HTMLButtonElement> {
    return this.mobileSpinnerUp$.value;
  }

  set mobileSpinnerUp(elementRef: ElementRef<HTMLButtonElement>) {
    this.mobileSpinnerUp$.next(elementRef);
  }

  @ViewChild('mobileSpinnerDown', {static: false})
  get mobileSpinnerDown(): ElementRef<HTMLButtonElement> {
    return this.mobileSpinnerDown$.value;
  }

  set mobileSpinnerDown(elementRef: ElementRef<HTMLButtonElement>) {
    this.mobileSpinnerDown$.next(elementRef);
  }

  static toFixed(value: number, precision: number = 0): number {
    const power = Math.pow(10, precision);

    return Math.round(value * power) / power;
  }

  static parseValue(value: number | string | BigNumber): number | BigNumber {
    const alignedValue = value.toString().trim();

    /**
     * Condition to identify dot at the end of string or more than two dots.
     * Need to be applied because parseFloat will remove dot in the end for value like this: '5.'
     */
    if (alignedValue.indexOf('.') === alignedValue.length - 1 || !alignedValue.match(/^[^.]*\.?[^.-]*$/) || isNaN(+alignedValue)) {
      return NaN;
    }

    return isSafeNumber(alignedValue) ? parseFloat(alignedValue) : new BigNumber(alignedValue);
  }

  constructor(@Optional() @Self() public ngControl: NgControl,
              counterConfig: AdaptRxCounterConfigService,
              _changeDetectorRef: ChangeDetectorRef,
              private _deviceDetection: AdaptDeviceDetectionService,
              private _renderer: Renderer2,
              private _elRef: ElementRef,
              private _ngZone: NgZone,
              public ts: AdaptTranslateService,
              _adaptRadarService: AdaptRadarAngularService,
              _elem: ElementRef<HTMLElement>) {
    super(ngControl, ts, _adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptRxCounter, _changeDetectorRef);

    this.step = counterConfig.step;
    this.size = counterConfig.size;
  }

  ngOnInit(): void {
    super.ngOnInit();

    this.isMobile = this._deviceDetection.isMobile();

    this.setDefaultTexts(this.ts.getCurrentLanguage());
  }

  ngAfterViewInit(): void {
    super.ngAfterViewInit();

    fromEvent(this.inputEl.nativeElement, 'input')
      .pipe(takeUntil(this.destroy$))
      .subscribe((event: Event) => {
        this._setValue((event.target as HTMLInputElement).value);
      });

    fromEvent(this.inputEl.nativeElement, 'keypress')
      .pipe(takeUntil(this.destroy$))
      .subscribe((event: KeyboardEvent) => {
        if (this.nonInteractive) {
          return;
        }

        // allowing entering symbols that are generalized for the
        // Scientific, Float, or Integer numbers
        // This is how native <input> element of type number works.
        // https://developer.mozilla.org/en-US/docs/Web/HTML/Element/input/number.
        // The only difference - we are not limit the number of the allowed symbols to be entered.
        const regExp: RegExp = /^[-+.\de]?$/i;

        if (!regExp.test(event.key)) {
          event.preventDefault();
        }
      });

    fromEvent(this.inputEl.nativeElement, 'keydown')
      .pipe(takeUntil(this.destroy$))
      .subscribe((event: KeyboardEvent) => {
        if (this.nonInteractive) {
          return;
        }

        const keyCode = getEventKeyCode(event);

        if (keyCode === UP_ARROW || keyCode === DOWN_ARROW) {
          event.preventDefault();
          keyCode === UP_ARROW ? this.increaseCounter(true) : this.decreaseCounter(true);
        }
      });

    const focusIn$ = merge(
      fromEvent(this.inputEl.nativeElement, 'focus').pipe(),
      this.spinnerUp$.pipe(
        switchMap(spinnerUp => spinnerUp ? fromEvent(spinnerUp.nativeElement, 'focus') : EMPTY)
      ),
      this.spinnerDown$.pipe(
        switchMap(spinnerDown => spinnerDown ? fromEvent(spinnerDown.nativeElement, 'focus') : EMPTY)
      ),
      this.mobileSpinnerUp$.pipe(
        switchMap(spinnerUp => spinnerUp ? fromEvent(spinnerUp.nativeElement, 'focus') : EMPTY)
      ),
      this.mobileSpinnerDown$.pipe(
        switchMap(spinnerDown => spinnerDown ? fromEvent(spinnerDown.nativeElement, 'focus') : EMPTY)
      )
    );

    const focusOut$ = merge(
      fromEvent(this.inputEl.nativeElement, 'blur').pipe(),
      this.spinnerUp$.pipe(
        switchMap(spinnerUp => spinnerUp ? fromEvent(spinnerUp.nativeElement, 'blur') : EMPTY)
      ),
      this.spinnerDown$.pipe(
        switchMap(spinnerDown => spinnerDown ? fromEvent(spinnerDown.nativeElement, 'blur') : EMPTY)
      ),
      this.mobileSpinnerUp$.pipe(
        switchMap(spinnerUp => spinnerUp ? fromEvent(spinnerUp.nativeElement, 'blur') : EMPTY)
      ),
      this.mobileSpinnerDown$.pipe(
        switchMap(spinnerDown => spinnerDown ? fromEvent(spinnerDown.nativeElement, 'blur') : EMPTY)
      )
    ).pipe(
      filter(() => {
        return document.activeElement !== this.inputEl.nativeElement &&
          (!this.spinnerUp || document.activeElement !== this.spinnerUp.nativeElement) &&
          (!this.spinnerDown || document.activeElement !== this.spinnerDown.nativeElement) &&
          (!this.mobileSpinnerUp || document.activeElement !== this.mobileSpinnerUp.nativeElement) &&
          (!this.mobileSpinnerDown || document.activeElement !== this.mobileSpinnerDown.nativeElement);
      })
    );

    const focusChanged = merge(focusIn$, focusOut$).pipe(
      debounceTime(0), // btw leaving the old element and entering the new element the active element is indeed the document/body itself
      distinctUntilChanged((previousEvent: FocusEvent, currentEvent: FocusEvent) => {
        return previousEvent.type === currentEvent.type;
      })
    );

    this._ngZone.runOutsideAngular(() => {
      focusChanged.pipe(
        takeUntil(this.destroy$)
      ).subscribe((e: FocusEvent) => {
        // we only return to the ngZone (triggering change detection) at this point because we know the focus has changed
        this._ngZone.run(() => {
          if (e.type === 'focus') {
            this.focused = true;
            this._sendEvent(AdaptRadarAngularCustomEventType.Focus);
            super.focusHandler(e);
          } else {
            this.focused = false;
            this._sendEvent(AdaptRadarAngularCustomEventType.Blur);
            super.blurHandler(e);
            this._onTouched();
          }
          this._changeDetectorRef.detectChanges()
        });
      });
    });

  }

  setDefaultTexts(translation: KeyValueObject): void {
    this.counterTextsDefault.decrease = translation['adapt.counter.decrease'];
    this.counterTextsDefault.increase = translation['adapt.counter.increase'];
  }

  writeValue(value: number | BigNumber): void {
    this._value = isNil(value) ? null : value;
    this.inputEl.nativeElement.value = this.value;
  }

  toggleTooltip(tooltip: AdaptTooltipDirective, el: HTMLSpanElement): void {
    tooltip.delay = 0;

    if (!tooltip.isOpen() && el.offsetWidth >= el.scrollWidth) {
      return;
    }

    tooltip.isOpen() ? tooltip.close() : tooltip.open();
  }

  onAnimationIncreaseStart(event: AnimationEvent): void {
    if (event.phaseName === 'start') {
      this.increaseAnimationDone = false;
    }
  }

  onAnimationIncreaseDone(event: AnimationEvent): void {
    if (event.phaseName === 'done') {
      this.increaseAnimationDone = true;
    }
  }

  onAnimationDecreaseStart(event: AnimationEvent): void {
    if (event.phaseName === 'start') {
      this.decreaseAnimationDone = false;
    }
  }

  onAnimationDecreaseDone(event: AnimationEvent): void {
    if (event.phaseName === 'done') {
      this.decreaseAnimationDone = true;
    }
  }

  checkFormMobileSpinners(): void {
    if (this.isMobile && !this.disabled) {
      this._openMobileSpinners();
    }
  }

  increaseCounter(animationNeeded?: boolean): void {
    if (this.isMobile) {
      return;
    }

    this.increase();

    if (animationNeeded) {
      const animationTimeout = setTimeout(() => {
        this.animateArrowUp = false;
        clearTimeout(animationTimeout);
      }, parseFloat(ANIMATION_DURATION.fast) * 1000);

      this.animateArrowUp = true;
    }
  }

  decreaseCounter(animationNeeded?: boolean): void {
    if (this.isMobile) {
      return;
    }

    this.decrease();

    if (animationNeeded) {
      const animationTimeout = setTimeout(() => {
        this.animateArrowDown = false;
        clearTimeout(animationTimeout);
      }, parseFloat(ANIMATION_DURATION.fast) * 1000);

      this.animateArrowDown = true;
    }
  }

  increase(): void {
    this._sendEvent(AdaptRadarAngularCustomEventType.Click, 'increase');

    this._value = this._applyBoundaries(this._calculateNewValue(CounterOperations.INCREASE));

    this.inputEl.nativeElement.value = this.value;
    this._onChange(this._value);
  }

  decrease(): void {
    this._sendEvent(AdaptRadarAngularCustomEventType.Click, 'decrease');

    this._value = this._applyBoundaries(this._calculateNewValue(CounterOperations.DECREASE));

    this.inputEl.nativeElement.value = this.value;
    this._onChange(this._value);
  }

  inputClickHandler(event: Event): void {
    if (this.readonly) {
      event.preventDefault();
    }
  }

  protected _focusNativeElement(): void {
    this.inputEl.nativeElement.focus();
  }

  private _openMobileSpinners(): void {
    if (!this.isSpinnerMobile) {
      this.isSpinnerMobile = true;

      // Set timeout to skip 'open' click
      setTimeout(() => {
        this._removeDocumentClickListener = this._renderer.listen('document', 'click', (event: Event) => {
          if (!this.mobileActionsContainer.nativeElement.contains(event.target as HTMLElement)) {
            this._closeMobileSpinners();
          }
        });
      }, 0);
    }
  }

  private _closeMobileSpinners(): void {
    if (this.isSpinnerMobile) {
      this.isSpinnerMobile = false;
      this._removeDocumentClickListener();
    }
  }

  private _setValue(value: string | number): void {
    this._value = isNil(value) || value.toString().trim().length === 0
      ? null
      : AdaptRxCounterComponent.parseValue(value);

    this._onChange(this._value);
    this._changeDetectorRef.detectChanges();
  }

  private _getCurrentValuePrecision(): number {
    if (BigNumber.isBigNumber(this._value)) {
      return !this.precision && (this._value as BigNumber).isInteger()
        ? (this._value as BigNumber).dp()
        : this.precision;
    }

    return !this.precision && this._value % 1 !== 0
      ? this._value.toString().split(/[.]/)[1].length
      : this.precision;
  }

  private _calculateNewValue(operation: CounterOperations): number | BigNumber {
    // if counter value is null, undefined or NaN:
    //  - in case of INCREASE operation we should return min value passed to the component;
    //  - in case of DECREASE operation we should return max value passed to the component.
    // This is how native <input> element of type number works.
    // https://developer.mozilla.org/en-US/docs/Web/HTML/Element/input/number
    if (isNil(this._value) || (BigNumber.isBigNumber(this._value) ? this._value.isNaN() : isNaN(this._value))) {
      if (this.min && operation === CounterOperations.INCREASE) {
        return this.min;
      } else if (this.max && operation === CounterOperations.DECREASE) {
        return this.max;
      } else {
        return 0;
      }
    }

    const valuePrecision = this._getCurrentValuePrecision();

    switch (operation) {
      case CounterOperations.INCREASE:
        return BigNumber.isBigNumber(this._value)
          ? new BigNumber(this._value.plus(this.step).decimalPlaces(valuePrecision))
          : AdaptRxCounterComponent.toFixed(this._value + this.step, valuePrecision);

      case CounterOperations.DECREASE:
        return BigNumber.isBigNumber(this._value)
          ? new BigNumber(this._value.minus(this.step).decimalPlaces(valuePrecision))
          : AdaptRxCounterComponent.toFixed(this._value - this.step, valuePrecision);
    }
  }

  private _applyBoundaries(value: number | BigNumber): number | BigNumber {
    if (BigNumber.isBigNumber(value) ? value.lt(this.min) : value < this.min) {
      return this.min;
    }

    if (BigNumber.isBigNumber(value) ? value.gt(this.max) : value > this.max) {
      return this.max;
    }

    return value;
  }

  private _sendEvent(eventType: AdaptRadarAngularCustomEventType, target?: 'increase' | 'decrease'): void {
    const eventData: AdaptRadarAngularComponentRxCounterData = {
      GroupName: this.adaptRadarGroupName,
      EventType: eventType,
      Label: this.label,
      Subtext: !!this.subLabel,
      Target: target ?? null,
      // TODO: Need to be changed to track data from used directives or by checking validators applied to control
      NumberType: null,
      Required: this.required,
      TextEntered: !!this.value,
      CharCount: this.value?.length,
      Hinted: !!this.tooltip,
      Disabled: this.disabled,
      ReadOnly: this.readonly,
      FieldSize: this.size,
      // DynamicEntry: false
    };

    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptRxCounter, eventData);
  }
}
